.whole_page_loading {
  z-index: 100;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.whole_page_loading span {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-right: 15px;
  /* background: lightgreen; */
  background: black;
  transform-origin: right bottom;
  animation: load 1s ease infinite;
}
.whole_page_loading span:last-child {
  margin-right: 0px;
}
@keyframes load {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(90deg);
  }
}
.whole_page_loading span:nth-child(1) {
  animation-delay: 0.13s;
}
.whole_page_loading span:nth-child(2) {
  animation-delay: 0.26s;
}
.whole_page_loading span:nth-child(3) {
  animation-delay: 0.39s;
}
.whole_page_loading span:nth-child(4) {
  animation-delay: 0.52s;
}
.whole_page_loading span:nth-child(5) {
  animation-delay: 0.65s;
}
.whole_page_loading span:nth-child(6) {
  animation-delay: 0.78s;
}
.whole_page_loading span:nth-child(7) {
  animation-delay: 0.91s;
}
