.bgImg {
  border-radius: 30px;
  width: 35px;
  height: 35px;
  background-size: 35px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}

.bgImgNormal {
  border-radius: 30px;
  width: 60px;
  height: 60px;
  background-size: 60px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}

.bgImgLarge {
  border-radius: 50px;
  width: 70px;
  height: 70px;
  background-size: 70px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}
