.topbar_container {
  font-family: Lato, Helvetica Neue, Arial, Helvetica, sans-serif;
  min-height: 50px;
  height: 3vw;
  backdrop-filter: blur(5px);
  color: snow;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.9);
  background-color: rgba(255, 255, 255, 0.4);
  padding-left: 50px;
  padding-right: 50px;
}

